import React, { useState, useEffect } from 'react';
import ChatRoom from './components/ChatRoom';
import LoginForm from './components/LoginForm';
import InviteModal from './components/InviteModal';
import { ThemeProvider, CssBaseline, createTheme } from '@mui/material';

function App() {
  const [user, setUser] = useState(null);
  const [darkMode, setDarkMode] = useState(() => {
    return window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches;
  });
  const [inviteModalOpen, setInviteModalOpen] = useState(false);

  useEffect(() => {
    const root = window.document.documentElement;
    if (darkMode) {
      root.classList.add('dark');
    } else {
      root.classList.remove('dark');
    }
  }, [darkMode]);

  const handleLogin = (userData) => {
    setUser(userData);
  };
  const handleLogout = () => {
    setUser(null);
  };
  const handleToggleDarkMode = () => {
    setDarkMode((prev) => !prev);
  };

  const handleInvite = async (email) => {
    const res = await fetch('/api/invite', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ to: email, link: window.location.origin }),
    });
    if (!res.ok) throw new Error('Failed to send invite');
    return true;
  };

  const theme = createTheme({
    palette: {
      mode: 'light', // or 'dark'
      // Customize your palette here
    },
  });

  // NOTE: To ensure mobile support, add the following to your public/index.html <head>:
  // <meta name="viewport" content="width=device-width, initial-scale=1" />
  //
  // All interactive elements should support mouse, keyboard, and touch events in their respective components.
  //
  // The layout should use responsive CSS classes (e.g., Tailwind) in all components.
  //
  // Removed incorrect ThemeProvider usage from here. Use Tailwind or your CSS framework for theming.

  if (!user) {
    return <LoginForm onLogin={handleLogin} />;
  }

  return (
    <>
      <InviteModal open={inviteModalOpen} onClose={() => setInviteModalOpen(false)} onInvite={handleInvite} />
      <ChatRoom
        user={user}
        onLogout={handleLogout}
        darkMode={darkMode}
        onToggleDarkMode={handleToggleDarkMode}
        onOpenInvite={() => setInviteModalOpen(true)}
      />
    </>
  );
}

export default App; 