import React, { useState, useRef, useEffect } from 'react';
import Peer from 'simple-peer';
import io from 'socket.io-client';
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';

const SOCKET_URL = 'http://localhost:5000';

export default function ScreenShare({ user }) {
  const [sharing, setSharing] = useState(false);
  const [remoteStream, setRemoteStream] = useState(null);
  const [sharer, setSharer] = useState(null);
  const [upcoming, setUpcoming] = useState(false);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'info' });
  const socketRef = useRef();
  const peerRef = useRef();
  const videoRef = useRef();

  useEffect(() => {
    socketRef.current = io(SOCKET_URL);
    socketRef.current.on('signal', async ({ from, data }) => {
      if (peerRef.current) {
        peerRef.current.signal(data);
      } else if (data.type === 'offer') {
        // Create peer as receiver
        const peer = new Peer({ initiator: false, trickle: false });
        peer.on('signal', (signal) => {
          socketRef.current.emit('signal', { to: from, from: socketRef.current.id, data: signal });
        });
        peer.on('stream', (stream) => {
          setRemoteStream(stream);
          setSharer(from);
        });
        peer.signal(data);
        peerRef.current = peer;
      }
    });
    // Listen for screen share upcoming event
    socketRef.current.on('screen_share_upcoming', ({ username }) => {
      if (username !== user.username) {
        setSnackbar({ open: true, message: `${username} is about to share their screen...`, severity: 'info' });
        setUpcoming(true);
        setTimeout(() => setUpcoming(false), 5000);
      }
    });
    return () => {
      socketRef.current.disconnect();
      if (peerRef.current) peerRef.current.destroy();
    };
  }, [user.username]);

  // Add this useEffect to set video srcObject
  useEffect(() => {
    if (videoRef.current && remoteStream) {
      videoRef.current.srcObject = remoteStream;
    }
  }, [remoteStream]);

  const startScreenShare = async () => {
    // Notify others that screen sharing is about to start
    socketRef.current.emit('screen_share_upcoming', { username: user.username });
    try {
      const stream = await navigator.mediaDevices.getDisplayMedia({ video: true, audio: false });
      setSharing(true);
      // Create peer as initiator
      const peer = new Peer({ initiator: true, trickle: false, stream });
      peer.on('signal', (signal) => {
        // Broadcast to all (for demo, send to 'all')
        socketRef.current.emit('signal', { to: 'all', from: socketRef.current.id, data: signal });
      });
      peer.on('stream', (remote) => {
        setRemoteStream(remote);
      });
      peerRef.current = peer;
      // End share if user stops
      stream.getVideoTracks()[0].onended = () => {
        setSharing(false);
        peer.destroy();
        peerRef.current = null;
        setRemoteStream(null);
        setSharer(null);
      };
    } catch (err) {
      alert('Screen share failed: ' + err.message);
    }
  };

  return (
    <div className="my-2 flex flex-col items-center">
      {!sharing && !remoteStream && (
        <button
          onClick={startScreenShare}
          className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded shadow font-semibold"
        >
          Share Screen
        </button>
      )}
      {remoteStream && (
        <div className="w-full max-w-md mt-2">
          <video
            ref={videoRef}
            autoPlay
            playsInline
            controls={false}
            className="rounded shadow w-full"
            onLoadedMetadata={() => videoRef.current && videoRef.current.play()}
          />
          <div className="text-xs text-gray-500 mt-1">{sharer ? 'Screen shared by another user' : ''}</div>
        </div>
      )}
      {sharing && <div className="text-xs text-blue-500 mt-2">You are sharing your screen</div>}
      {/* Snackbar for upcoming screen share */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={4000}
        onClose={() => setSnackbar(s => ({ ...s, open: false }))}
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      >
        <Alert onClose={() => setSnackbar(s => ({ ...s, open: false }))} severity={snackbar.severity} sx={{ width: '100%' }}>
          {snackbar.message}
        </Alert>
      </Snackbar>
    </div>
  );
} 