import React, { useState, useCallback } from 'react';
import { useDropzone } from 'react-dropzone';
import { Box, Paper, Typography, Avatar, TextField, Button, IconButton, Stack } from '@mui/material';
import { PhotoCamera, Person, TagFaces } from '@mui/icons-material';

const defaultAvatar = 'https://ui-avatars.com/api/?name=User&background=random';

export default function LoginForm({ onLogin }) {
  const [username, setUsername] = useState('');
  const [avatar, setAvatar] = useState(null);
  const [mode, setMode] = useState('login'); // 'login' or 'signup'

  // Dropzone for avatar upload
  const onDrop = useCallback((acceptedFiles) => {
    if (acceptedFiles && acceptedFiles[0]) {
      const file = acceptedFiles[0];
      const reader = new FileReader();
      reader.onload = (e) => setAvatar(e.target.result);
      reader.readAsDataURL(file);
    }
  }, []);
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop, accept: { 'image/*': [] } });

  // Store user in localStorage for persistence
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!username.trim()) return;
    const userData = {
      username: username.trim(),
      avatar: avatar || defaultAvatar,
    };
    localStorage.setItem('chatapp_user', JSON.stringify(userData));
    onLogin(userData);
  };

  // Try to load user from localStorage on mount
  React.useEffect(() => {
    const saved = localStorage.getItem('chatapp_user');
    if (saved) {
      try {
        const user = JSON.parse(saved);
        if (user.username) {
          setUsername(user.username);
          setAvatar(user.avatar);
        }
      } catch {}
    }
  }, []);

  // Validation state
  const [touched, setTouched] = useState(false);
  const isSignup = mode === 'signup';
  const isValid = username.trim().length > 2;
  const [bgColor, setBgColor] = useState(() => localStorage.getItem('bubblytalk_bg_color') || '#f8fafc');

  // Handle color picker
  const handleColorChange = (e) => {
    setBgColor(e.target.value);
    localStorage.setItem('bubblytalk_bg_color', e.target.value);
  };

  React.useEffect(() => {
    if (bgColor) {
      document.body.style.background = bgColor;
    }
    return () => {
      document.body.style.background = '';
    };
  }, [bgColor]);

  return (
    <Box sx={{ minHeight: '100vh', width: '100vw',
      background: bgColor,
      display: 'flex', alignItems: 'center', justifyContent: 'center', position: 'relative', overflow: 'hidden' }}>
      {/* Color picker circle */}
      <Box sx={{ position: 'fixed', bottom: 24, left: 24, zIndex: 10, display: 'flex', flexDirection: 'column', gap: 2, alignItems: 'center' }}>
        <label style={{ width: 48, height: 48, borderRadius: '50%', background: bgColor, boxShadow: '0 2px 8px #0002', display: 'flex', alignItems: 'center', justifyContent: 'center', cursor: 'pointer', border: '2px solid #fff' }} title="Pick background color">
          <input
            type="color"
            value={bgColor}
            onChange={handleColorChange}
            style={{ opacity: 0, width: 48, height: 48, cursor: 'pointer', position: 'absolute' }}
            aria-label="Pick background color"
          />
          <span style={{ width: 24, height: 24, borderRadius: '50%', border: '2px solid #ccc', background: '#fff', display: 'block' }}></span>
        </label>
      </Box>
      {/* Animated background shapes for style */}
      <Box sx={{ position: 'absolute', inset: 0, zIndex: 0, pointerEvents: 'none' }}>
        <Box sx={{ position: 'absolute', top: -60, left: -60, width: 288, height: 288, bgcolor: 'primary.light', opacity: 0.18, borderRadius: '50%', filter: 'blur(16px)' }} />
        <Box sx={{ position: 'absolute', bottom: -80, right: -80, width: 384, height: 384, bgcolor: 'secondary.light', opacity: 0.13, borderRadius: '50%', filter: 'blur(16px)' }} />
        <Box sx={{ position: 'absolute', top: '50%', left: '50%', width: 160, height: 160, bgcolor: 'info.light', opacity: 0.09, borderRadius: '50%', filter: 'blur(12px)', transform: 'translate(-50%,-50%)' }} />
      </Box>
      <Paper elevation={16} sx={{
        zIndex: 1,
        p: 5,
        borderRadius: 6,
        minWidth: 340,
        maxWidth: 420,
        width: '100%',
        backdropFilter: 'blur(16px)',
        background: 'rgba(255,255,255,0.92)',
        boxShadow: '0 8px 32px 0 rgba(31, 38, 135, 0.18)',
        border: '1px solid rgba(255,255,255,0.25)',
      }}>
        <Stack spacing={3} alignItems="center">
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 1 }}>
            <TagFaces color="primary" sx={{ fontSize: 56, animation: 'wiggle 1.5s infinite', mb: 1, filter: 'drop-shadow(0 2px 8px #a1c4fd)' }} />
            <Typography variant="h4" fontWeight={800} textAlign="center" color="primary.main" letterSpacing={1}>
              {isSignup ? 'Create Account' : 'Welcome Back!'}
            </Typography>
            <Typography variant="subtitle1" color="text.secondary" textAlign="center" sx={{ fontWeight: 500 }}>
              {isSignup ? 'Sign up and start chatting!' : 'Sign in to join the chat'}
            </Typography>
          </Box>
          <form onSubmit={handleSubmit} style={{ width: '100%' }}>
            <Stack spacing={3} alignItems="center">
              {isSignup && (
                <Box {...getRootProps()} sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', cursor: 'pointer', gap: 1 }}>
                  <input {...getInputProps()} />
                  <Avatar src={avatar || undefined} sx={{ width: 90, height: 90, mb: 1, border: 4, borderColor: 'primary.main', boxShadow: 4, transition: '0.2s', '&:hover': { boxShadow: 8, borderColor: 'secondary.main' } }}>
                    {!avatar && <PhotoCamera fontSize="large" color="disabled" />}
                  </Avatar>
                  <Typography variant="caption" color="text.secondary">
                    {isDragActive ? 'Drop image here...' : 'Click or drag to upload avatar'}
                  </Typography>
                </Box>
              )}
              <TextField
                label="Username"
                value={username}
                onChange={e => { setUsername(e.target.value); setTouched(true); }}
                required
                fullWidth
                InputProps={{ startAdornment: <Person sx={{ mr: 1, color: 'text.secondary' }} /> }}
                sx={{ bgcolor: 'rgba(255,255,255,0.7)', borderRadius: 2 }}
                error={touched && !isValid}
                helperText={touched && !isValid ? 'Username must be at least 3 characters' : ' '}
              />
              <Button
                type="submit"
                variant="contained"
                color={isSignup ? 'primary' : 'secondary'}
                fullWidth
                size="large"
                disabled={!isValid}
                sx={{ fontWeight: 700, letterSpacing: 1, py: 1.5, borderRadius: 3, boxShadow: 3, textTransform: 'none', fontSize: 18 }}
              >
                {isSignup ? 'Sign Up' : 'Login'}
              </Button>
              <Button
                type="button"
                variant="text"
                color="info"
                fullWidth
                onClick={() => { setMode(isSignup ? 'login' : 'signup'); setTouched(false); }}
                sx={{ textTransform: 'none', fontWeight: 600 }}
              >
                {isSignup ? 'Already have an account? Login' : "Don't have an account? Sign Up"}
              </Button>
            </Stack>
          </form>
        </Stack>
      </Paper>
      <style>{`
        @keyframes wiggle { 0%,100%{transform:rotate(-8deg);} 50%{transform:rotate(8deg);} }
      `}</style>
    </Box>
  );
} 